.product-hero header{display:grid;grid-gap:48px;grid-template-columns:minmax(420px,480px) 640px;grid-template-rows:420px;justify-content:center;margin:0 auto 120px;max-width:1080px;width:96%}.product-hero button{background:#0a84ff;border:0;border-radius:4px;color:#fff;cursor:pointer;font-size:24px;padding:16px}.product-hero button:focus,.product-hero button:hover{background:#0060df}.product-hero .product-hero__content{align-items:flex-start;display:flex;flex-direction:column;justify-content:center}.product-hero .product-hero__content h1{font-size:54px}.product-hero .product-hero__content p{font-size:28px;font-weight:700;opacity:.8}.product-hero .product-hero__content .product-hero__image{align-items:center;display:flex}