.product .product__section{margin:60px 0;padding:24px;position:relative}.product .product__section:nth-child(2n){background:linear-gradient(#e1eff8,#cbe3f5)}.product .product__section:nth-child(2n) .product__section-top{background-size:100% 60px;height:60px;left:0;position:absolute;right:0;top:-60px}.product .product__section:nth-child(2n) .product__section-bottom{background-position:top;background-size:100% 60px;bottom:-60px;height:60px;left:0;position:absolute;right:0}.product .product__section:nth-child(2n) .product__section-content{grid-column:2/3;grid-row:1/-1}.product .product__section:nth-child(2n) .product__section-image{grid-column:1/2;grid-row:1/-1}.product .product__section section{grid-template-columns:440px 440px;grid-template-rows:400px}.product .product__section .product__section-content{display:flex;flex-direction:column;justify-content:center}.product .product__section .product__section-content p{font-size:22px}.product .product__section .product__section-image{align-items:center;align-self:center;display:flex;height:320px}